body {
  font-size: 14px;
  color: #0f0a00;
  font-family: "Montserrat", sans-serif; }

a {
  text-decoration: none;
  color: #0f0a00; }

ul {
  list-style: none; }

.modal-content {
  background-color: #f5f8f1; }

.page-header {
  background-color: #f5f8f1;
  background-image: url("../img/bg-promo-desktop.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }
  @media (max-width: 767px) {
    .page-header {
      background-image: none; } }

.page-header__promo {
  min-height: 660px; }
  @media (max-width: 991px) {
    .page-header__promo {
      min-height: 530px; } }
  @media (max-width: 767px) {
    .page-header__promo {
      flex-direction: column;
      padding-top: 50px; } }

.page-header__slogan {
  font-size: 48px;
  font-weight: 600;
  line-height: 52px;
  letter-spacing: 0.96px; }
  @media (max-width: 991px) {
    .page-header__slogan {
      font-size: 36px; } }
  @media (max-width: 767px) {
    .page-header__slogan {
      text-align: center;
      font-size: 26px; } }

.page-header__text {
  background-color: #edce47; }

.page-header__link {
  display: block;
  text-align: center;
  padding: 21px 0;
  width: 186px;
  background-color: #edce47;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1.6px; }
  @media (max-width: 991px) {
    .page-header__link {
      padding: 14px 0;
      width: 157px; } }
  @media (max-width: 767px) {
    .page-header__link {
      margin: 0 auto; } }

.carousel__ind {
  flex-direction: column;
  position: relative; }
  .carousel__ind .carousel__rect {
    background-color: #9bb968; }
  @media (max-width: 767px) {
    .carousel__ind {
      flex-direction: row;
      position: absolute;
      bottom: -40px; } }

.page-header__img {
  object-fit: contain; }

.navbar__logo {
  width: 130px; }

.navbar-toggler:hover, .navbar-toggler:focus {
  outline: none; }

.navbar-toggler-icon {
  background: url("../img/sprite.svg#s-icon-burger") top left no-repeat; }

@media (max-width: 991px) {
  .nav-link {
    font-size: 12px; } }

.nav-item__phone {
  padding: 0 8px; }
  @media (max-width: 991px) {
    .nav-item__phone {
      padding: 0; } }

.navbar__phone {
  color: #84a748;
  font-size: 20px;
  font-weight: 600; }
  @media (max-width: 991px) {
    .navbar__phone {
      font-size: 12px; } }

.navbar__call {
  padding: 0 40px;
  font-size: 16px;
  font-weight: 600;
  border: 1px solid #9bb968;
  padding: 15px 0; }
  @media (max-width: 991px) {
    .navbar__call {
      font-size: 13px; } }
